import tabBar from '../../components/tabbar';
import { formatDate } from '../../libs/tools.js';
import { isLogin } from '../../libs/authService.js'
import { home } from '../../api/api';
export default {
	components:{
		tabBar
	},
	data() {
		return {
			collectionNum: 0,
			customerNum: 0,
			newCustomerNum: 0,
			opportunities: [],
			orderAmount: 0,
			visitNum: 0
		};
	},
	onShow() {
		// 未登录跳转
		if( !isLogin() ) {
			console.log('111')
			uni.navigateTo({
				url: "../login/index"
			})
		}else{
			home().then(res =>{
				console.log('res=',res)
				let _d = res.data
				this.collectionNum = _d.collectionNum;
				this.customerNum = _d.customerNum;
				this.newCustomerNum = _d.newCustomerNum;
				this.opportunities = _d.opportunities;
				this.orderAmount = _d.orderAmount;
				this.visitNum = _d.visitNum;
			})
		}
	},
	onLoad() {
		
	}
}
